import React from 'react';
import '../scss/_header.scss'
import '../scss/_footer.scss'
import '../scss/_product.scss'

import P_product from '../images/inkwang_img/p_product.jpeg'
import pm_1 from '../images/pm1.jpeg'
import pm_2 from '../images/pm2.jpeg'
import pm_3 from '../images/pm3.jpeg'
import pm_4 from '../images/pm4.jpeg'
import Back from '../images/back11.jpg'

import PropTypes from 'prop-types';
import { useTheme } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';

import { NavLink, useLocation } from 'react-router-dom';
import SidebarItem from '../SidebarItem';

const Product = () => {

  function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box sx={{ p: 3 }}>
            <Typography>{children}</Typography>
          </Box>
        )}
      </div>
    );
  }
  
  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
  };
  
  function a11yProps(index) {
    return {
      id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
    };
  }


  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const pathName = useLocation().pathname;

    const menus = [
        { name: "", path: "/product" },
        { name: " ", path: "/product" },
        { name: " ", path: "/product_r" },
    ];

  return (
    <div className='content'>
      <div className='back_img'>
        <img src={Back} alt='#'/>
        <h3>Product</h3>
      </div>
      <div className='side_come'>
        <div className= "Side">
          {menus.map((menu, index) => {
              return(
                  <NavLink to={menu.path} key={index}>
                      <SidebarItem className="sidemenu"
                          menu={menu}
                          isActive={pathName === menu.path ? true : false}
                      />
                  </NavLink>
              );
          })}
        </div>
        <div className='product'>
          <div className="box">
            <Box sx={{ width: 700 }}>
              <AppBar position="static" style={{backgroundColor:'#fff',  boxShadow:"none"}}>
                <Tabs
                  value={value}
                  onChange={handleChange}
                  indicatorColor="gray"
                  textColor="inherit"
                  variant="scrollable"
                  aria-label="full width tabs example"
                  sx={{ borderBottom: 1, borderColor: '#d4d4d4'}}
                >
                  <Tab id="p_tab1" sx={{backgroundColor:"#15137C", fontSize:"18px", fontFamily:"NanumSquareBold", width:'25%', borderStartEndRadius:'80px 80px;', opacity:'1', zIndex:"20"}} label=" " {...a11yProps(0)} />
                  <Tab id="p_tab2" sx={{backgroundColor:"#7e7ccf", fontSize:"18px", fontFamily:"NanumSquareBold", width:'25%', borderStartEndRadius:'80px 80px;', left:'-7%', zIndex:"10"}} label=" " {...a11yProps(1)} />
                </Tabs>
              </AppBar>
                <TabPanel value={value} index={0} dir={theme.direction}>
                  <div className='pr_fill'>
                    <img className="pr_img" src={P_product} alt='/'/>
                    <pre>
                      -  :        ,     <br/>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;     <br/>
                      -  : <div id='pr_div'> <br/>    <br/>  <br/>  <br/></div>
                      -  :   /  /  /  
                    </pre>
                  </div>
                </TabPanel>
                <TabPanel value={value} index={1} dir={theme.direction}>
                  <div className='pr_fill2' style={{flexFlow:"column wrap"}}>
                    <img className="pm_4" src={pm_4} alt='/'/>
                    <img className="pm_2" src={pm_2} alt='/'/>
                    <img className="pm_1" src={pm_1} alt='/'/>
                    <img className="pm_3" src={pm_3} alt='/'/>
                  </div>
                </TabPanel>
            </Box>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Product;